import React from 'react';
import logo from './logo.png';
function Logo(props) {
  return (
    <div>
      <img
        src={logo}
        alt={'logo'}
        className={props.logoClassName}
      />
      {
        props.hasText &&
        <div>DOGify</div>
      }
    </div>
  )
}

export default Logo;


