import React, { Component } from 'react';
import Heading from '../../common/components/Heading/Heading';
import axios from 'axios';
import styles from './RandomImage.module.scss'
import ChooseBreed from '../../common/components/ChooseBreed/ChooseBreed'
import Wait from '../../common/components/Wait/Wait'
import { getImageFromLocalStorage } from "../../common/utils/localStorage";
import { buttonsTexts, headingTexts } from "../../common/config/dict";

class RandomImage extends Component {
  constructor(props) {
    super(props);
    this.onGoClick = this.onGoClick.bind(this);
    this.handleAddButtonClick = this.handleAddButtonClick.bind(this);
    this.state = {
      randomImg: '',
      isLoading: false,
      clickedLink: ''
    };
  }

  onGoClick(breed) {
    this.setState({ isLoading: true });
    axios.get(`https://dog.ceo/api/breed/${breed}/images/random`)
      .then(res => {
        this.setState({
          randomImg: res.data.message,
          isLoading: false
        })
      });
  }

  handleAddButtonClick(image) {
    this.props.onAddClick(image);
    this.setState({ clickedLink: image});
    setTimeout(
      () => this.setState({ clickedLink: '' }), 2000
    )
  }

  render() {
    return (
      <div className={styles.wrapper}>
        <Heading text={headingTexts.randomImage} />
        <ChooseBreed onGoClick={this.onGoClick}/>
        <Wait isWaiting={this.state.isLoading}>
          <div className={styles['dog-wrapper']}>
            <div className={this.state.randomImg === '' ? styles['no-display'] : styles['dog-element']}>
              <img
                src={this.state.randomImg}
                alt={'dog'}
                className={
                  `
                    ${styles['dog-img']}
                    ${this.state.randomImg === this.state.clickedLink
                      ? styles.rotate
                      : ''
                    }
                  `
                }
              />
              <button
                className={getImageFromLocalStorage(this.state.randomImg)
                  ? styles['dogified']
                  : styles['add-button']
                }
                onClick={() => this.handleAddButtonClick(this.state.randomImg)}
              >
                    <span className={styles['add-text']}>
                      {getImageFromLocalStorage(this.state.randomImg)
                        ? buttonsTexts.dogifiedButtonText
                        : buttonsTexts.addButtonText}
                    </span>
                <span className={styles['add-icon']}>
                      <i className={"heart outline large icon"}/>
                    </span>
              </button>
            </div>
          </div>
        </Wait>
      </div>
    )
  }
}

export default RandomImage


