import React, { Component } from 'react';
import Heading from '../../common/components/Heading/Heading';
import axios from 'axios';
import styles from './FindByBreed.module.scss';
import ChooseBreed from '../../common/components/ChooseBreed/ChooseBreed'
import Wait from '../../common/components/Wait/Wait'
import Pagination from '../../common/components/Pagination/Pagination'
import { getImageFromLocalStorage } from '../../common/utils/localStorage';
import { buttonsTexts, headingTexts } from "../../common/config/dict";

class FindByBreed extends Component {
  constructor(props) {
    super(props);
    this.onGoClick = this.onGoClick.bind(this);
    this.handlePageClick = this.handlePageClick.bind(this);
    this.handleNumberOfImagesToShow = this.handleNumberOfImagesToShow.bind(this);
    this.state = {
      listOfLinks: [],
      isLoading: false,
      imagesToShow: 9,
      boundaries: [0, 9],
      clickedLink: ''
    };
  }

  onGoClick(breed) {
    this.setState({ isLoading: true });
    axios.get(`https://dog.ceo/api/breed/${breed}/images`)
      .then(res => {
        this.setState({
          listOfLinks: res.data.message,
          isLoading: false
        })
      })
  }

  handleAddButtonClick(imgLink) {
    if(!getImageFromLocalStorage(imgLink)) {
      this.props.onAddClick(imgLink);
      this.setState({ clickedLink: imgLink });
      setTimeout(
        () => this.setState({ clickedLink: '' }), 2000
      )
    }
  }

  handlePageClick(newBoundaries) {
    this.setState({ boundaries: newBoundaries })
  }

  handleNumberOfImagesToShow(imagesNumber) {
    this.setState({ imagesToShow: imagesNumber});
    this.setState({ boundaries: [0, imagesNumber]})
  }

  render() {

    const imagesToShow = this.state.listOfLinks.slice(this.state.boundaries[0], this.state.boundaries[1]);

    return (
      <div className={styles.wrapper}>
        <Heading text={headingTexts.findByBreed} />
        <ChooseBreed onGoClick={this.onGoClick}/>
        <Wait isWaiting={this.state.isLoading}>
          <div className={styles['dogs-wrapper']}>
            {
              imagesToShow.map(link => (
                <div
                  key={link}
                  className={styles['dog-element']}
                >
                  <img
                    src={link}
                    alt={'dog'}
                    key={link}
                    className={
                      `
                        ${styles['dog-img']}
                        ${link === this.state.clickedLink
                          ? styles.rotate
                          : ''
                        }
                      `
                    }
                  />
                  <button
                    className={getImageFromLocalStorage(link)
                      ? styles['dogified']
                      : styles['add-button']
                    }
                    onClick={() => this.handleAddButtonClick(link)}
                  >
                    <span className={styles['add-text']}>
                      {getImageFromLocalStorage(link)
                        ? buttonsTexts.dogifiedButtonText
                        : buttonsTexts.addButtonText}
                    </span>
                    <span className={styles['add-icon']}>
                      <i className={"heart outline large icon"}/>
                    </span>
                  </button>
                </div>
              ))
            }
            <Pagination
              totalImages={this.state.listOfLinks.length}
              imagesToShow={this.state.imagesToShow}
              onPageClick={this.handlePageClick}
              onNumberOfImagesToShowClick={this.handleNumberOfImagesToShow}
            />
          </div>
        </Wait>
      </div>
    )
  }
}

export default FindByBreed


