import React from 'react';
import styles from './Navigation.module.scss'
import Logo from '../Logo/Logo';

function Navigation(props) {
  return (
    <nav className={styles.navigation}>
      <Logo hasText={true} logoClassName={styles.logo}/>
      <ul className={styles.menu}>
        {
          props.links.map((linkElem) => (
            <li
              key={linkElem.id}
              className={linkElem.id === props.currentLinkId ? styles['active-item'] : styles.item}
              onClick={() => props.onLinkClick(linkElem.id)}
            >
              <div className={styles['menu-icon']}>
                {linkElem.id === 'start' ? <i className="small home icon"/> : ''}
                {linkElem.id === 'find-by-breed' ? <i className="small search icon"/> : ''}
                {linkElem.id === 'random-img' ? <i className="small image icon"/> : ''}
                {linkElem.id === 'my-images' ? <i className="small images icon"/> : ''}
              </div>
              {linkElem.link}
              <div className={linkElem.id === 'my-images' ? styles['img-no-label'] : styles.invisible}>
                #{(props.listOfImages || []).length}
              </div>
            </li>
          ))
        }
      </ul>
    </nav>
  )
}

export default Navigation;


